body {
  padding: 20px;
  font-family: Montserrat, sans-serif;
}

.breed-select {
  width: 300px;
  background-color: #f0f8ff;
  font-family: Montserrat;
}

img {
  box-sizing: border-box;
  height: cover;
  display: block;
}

.cat-info-wrap {
  width: 800px;
  align-items: center;
  margin-top: 40px;
  display: flex;
}

.cat-info-text {
  margin-left: 30px;
}

.loader {
  width: 80%;
  border: 1px solid #fff;
  border-radius: 10px;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  width: 40px;
  height: 2px;
  box-sizing: border-box;
  background: #ff3d00;
  animation: animloader 2s linear infinite;
  position: absolute;
  top: 17px;
  left: 0;
}

@keyframes animloader {
  0% {
    left: 0;
    transform: translateX(-100%);
  }

  100% {
    left: 100%;
    transform: translateX(0%);
  }
}

